
import { onUnmounted, ref, watch } from "vue";
import { useAccount } from "@/hooks/useAccount";
import { useSubscription } from "@/hooks/useSubscription";
import { useDevices } from "@/hooks/useDevices";
import { Keyring } from "@polkadot/keyring";
import { encodeAddress } from "@polkadot/util-crypto";

export default {
  setup() {
    const controller = ref("");
    const seed = ref("");
    const { account, unsubscribe } = useAccount();

    onUnmounted(() => {
      unsubscribe();
    });

    const subscription = useSubscription(account);

    const { devices, loadDevices } = useDevices(account);

    watch(devices, () => {
      if (devices.value.length) {
        controller.value = devices.value[0].address;
      } else {
        controller.value = "";
      }
    });

    return {
      controller,
      seed,
      account,
      subscription,
      devices,
      loadDevices
    };
  },
  emits: ["controller"],
  computed: {
    controllerAccount() {
      if (this.seed) {
        try {
          const k = new Keyring();
          return k.addFromUri(this.seed, {}, "ed25519");
        } catch (error) {
          console.log(error);
        }
      }
      return null;
    },
    validateUri() {
      if (
        this.controllerAccount &&
        this.controller &&
        encodeAddress(this.controller) ===
          encodeAddress(this.controllerAccount.address)
      ) {
        return true;
      }
      return false;
    }
  },
  watch: {
    validateUri: {
      immediate: true,
      handler() {
        this.$emit("controller", this.controllerAccount);
      }
    }
  }
};
